import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Subscription } from 'Rxjs';
import { Observable ,  Subject } from 'rxjs';

import { AuthService } from '../../shared/services/auth.service';

import { User } from '../../shared/models/user';

import * as userReducer from '../store/user.reducer';
import * as userActions from '../store/user.actions';
import { UserState } from '../../store/app.state';
import { Store } from '@ngrx/store';
import {Actions, ofType} from '@ngrx/effects';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, OnDestroy {
  user: User = new User();
  // store
  // s_user: Observable<User[]>;
  userState$: Observable<any>;

  public form: FormGroup;
  public hasFailed = false;
  loggedInSub: Subscription;

  loginError = false;

  destroySubject$: Subject<void> = new Subject();

  constructor(
      private fb: FormBuilder,
      private router: Router,
      private store: Store<UserState>,
      private auth: AuthService,
      updates$: Actions
  ) {
    const thiObj = this;
    updates$
      .pipe(
        ofType(userActions.USER_401),
        takeUntil(this.destroySubject$)
      )
      .subscribe(() => {
        this.loginError = true;
        setTimeout(() => { this.loginError = false; }, 1000);
      });
  }


  ngOnInit() {
    // get user authentication status
    this.loggedInSub = this.auth.loggedIn$.pipe(takeUntil(this.destroySubject$)).subscribe(
      loggedIn => {
        //this.userState$ = this.store.select(userReducer.getUser);
        this.store.dispatch(new userActions.GetUserSettings());

        // redirect if logged in
        this.router.navigate(['/user']);
      });

    this.form = this.fb.group ( {
      uname: [null , Validators.compose ( [ Validators.required ] )] ,
      password: [null , Validators.compose ( [ Validators.required ] )]
    } );
  }

  onSubmit() {
    // Grab values from form
    const username = this.form.get('uname').value;
    const password = this.form.get('password').value;

    const payload = {
      email: username,
      password: password
    };
    this.store.dispatch(new userActions.UserLogin(payload));

    /*this.auth
        .signIn(username, password)
        .subscribe(
            (response: User) => {
              console.log(response);
              this.auth.doSignIn(response);
              this.router.navigate(['/dashboard/default']);
            },
            (error) => {
              console.log(error.message);
              this.hasFailed = true;
            }
        );*/
  }

  ngOnDestroy() {
    this.destroySubject$.next();
  }

}
